export default [
  {
    title: "",
    layout: [
      {
        el: "grid",
        col: 1,
        item: [
          {
            // 
            el: "field", // 
            title: " id",
            field: "text", // 
            get(binding: any) {
              return binding.meta.id
            },
            callback: (binding: any, val: any) => {
              binding.meta = binding.meta || {}
              binding.meta.id = val
            }
          },
          {
            // 
            el: "field", // 
            title: " class",
            field: "textbox", // 
            get(binding: any) {
              return binding.meta.class || []
            },
            callback: (binding: any, val: any) => {
              binding.meta = binding.meta || {}
              binding.meta.class = val
            }
          }
        ]
      },
      {
        el: "grid",
        col: 1,
        label: "",
        item: [
          {
            el: "field", // 
            title: "",
            field: "grid-item", // 
            get(binding: any) {
              return binding.items
            },
            callback(binding: any, val: any) {
              binding.items = val
            }
          }
        ]
      },
      {
        el: "grid",
        col: 2,
        item: [
          {
            el: "field",
            title: "",
            field: "select",
            map: {
              top: "",
              middle: "",
              bottom: ""
            },
            get(binding: any) {
              return binding.meta.align || "middle"
            },
            callback(binding: any, val: any) {
              binding.meta = binding.meta || {}
              binding.meta.align = val
            }
          },
          {
            el: "field",
            title: "",
            field: "select",
            map: {
              start: "",
              center: "",
              end: ""
            },
            get(binding: any) {
              return binding.meta.justify || "end"
            },
            callback(binding: any, val: any) {
              binding.meta = binding.meta || {}
              binding.meta.justify = val
            }
          }
        ]
      }
    ]
  }
]
