export default [
    {
      title: "", // 
      layout: [
        {
          el: 'grid',
          col: 1,
          item: [
            {
              // 
              el: "field", // 
              title: " id",
              field: "text", // 
              get(binding: any) {
                return binding.meta.id
              },
              callback: (binding: any, val: any) => {
                binding.meta = binding.meta || {}
                binding.meta.id = val
              }
            },
            {
              // 
              el: "field", // 
              title: " class",
              field: "textbox", // 
              get(binding: any) {
                return (binding.meta.class)||[]
              },
              callback: (binding: any, val: any) => {
                binding.meta = binding.meta || {}
                binding.meta.class = val
              }
            }
          ]
        },
        {
          // 
          el: "grid",
          col: 2, // 
          item: [
            {
              // 
              el: "field", // 
              title: "",
              field: "text", // 
              get(binding:any) {
                return binding.meta?.width
              },
              callback: (binding: any, val: any) => {
                binding.meta = binding.meta || {}
                binding.meta.width = val
              }
            },
            {
              // 
              el: "field", // 
              title: "",
              field: "text", // 
              get(binding: any) {
                return binding.meta?.height
              },
              callback: (binding: any, val: any) => {
                binding.meta = binding.meta || {}
                binding.meta.height = val
              }
            }
          ]
        },
        {
          // 
          el: "grid",
          col: 4, // 
          item: [
            {
              // 
              el: "field", // 
              title: "",
              field: "select", // 
              map: {
                  '': 'NONE',
                  left: '',
                  center: '',
                  right: ''
              },
              get(binding: any) {
                return binding.meta?.float
              },
              callback: (binding: any, val: any) => {
                binding.meta.float = val
              }
            },  {
              // 
              el: "field", // 
              title: "",
              field: "number", // 
              get(binding: any) {
                return binding.meta?.border
              },
              callback: (binding: any, val: any) => {
                binding.meta = binding.meta || {}
                binding.meta.border = val
              }
            },  {
                // 
                el: "field", // 
                title: "",
                field: "color", // 
                get(binding: any) {
                  return binding.meta?.borderColor
                },
                callback: (binding: any, val: any) => {
                  binding.meta = binding.meta || {}
                  binding.meta.borderColor = val
                }
            },  {
                // 
                el: "field", // 
                title: "",
                field: "select", // 
                map: {
                    '': 'NONE',
                    dashed: '',
                    solid: ''
                },
                get(binding: any) {
                  return binding.meta?.borderStyle
                },
                callback: (binding: any, val: any) => {
                  binding.meta = binding.meta || {}
                  binding.meta.borderStyle = val
                }
            },  {
                // 
                el: "field", // 
                title: "",
                field: "color", // 
                get(binding: any) {
                  return binding.meta?.backgroundColor
                },
                callback: (binding: any, val: any) => {
                  binding.meta = binding.meta || {}
                  binding.meta.backgroundColor = val
                }
            }
          ]
        }
      ]
    },
    {
      title: "",
      layout: [
        {
          el: "block"
        }
      ]
    }
  ]
  